* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 700;
  font-style: normal;
}

.home{
  .logo{
    background-color: #fff;
    border-radius: 10px;
  }
}

.hero{
  height: 92vh;
  display: flex;
  justify-content: center;
  align-items: center;

  &__title {
    flex: 0 1 50%;
    color: black;
    margin-bottom: 5vh;
    
    #line-through {
      text-decoration: line-through;
      color: grey;
    }
  }
 
}

.value {
  border: 5px dashed violet;
  height: auto;
  padding: 50px;
  // height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5vh;

    button {
      margin-top: 10px;
      vertical-align: middle;
      color: white;
      font-size: 40px;
      height: 60px;
      width: 350px;
      border-radius: 30px;
      background-image: linear-gradient(to right, #00A0F5, #6926C5, #6926C5);
    }    
  }

  &__cols {
    height: 80vh;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    align-items: center;

    &-in {
      width: 50%;
    }
  }

}

.reservation {
  border: 2px dashed red;
  height: 40vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: red;

  &__btn {
    Form {
      background-color: red;
      width: 600px;
    }
    width: 600px;
    height: 200px;
    border: 2px solid green;
  }
  
  #center {
    border: 2px dotted blue;
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
}

.collab {
  // background-color: blue;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;

  &__logo {
    // background-color: red;
    width: 30%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  &__text {
    // background-color: green;
    width: 30%;
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;

    ul {
      list-style-type: none;
    }
  }
}

.faq {
  height: 50vh;
  padding: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  ul {
    list-style-type: none;
  }
}

.children {
  display: flex;
  justify-content: center;
}
